// @ts-nocheck
import React, { useCallback } from 'react';


export interface IFirebaseContext {
	getProfile: (address: string) => Promise<IFetchResponseBase>;
	setProfile: (address: string, username: string, avatar: string) => Promise<IFetchResponseBase>;
	claim: (address: string) => Promise<IFetchResponseBase>;
	prepareMint: (address: string, receiver: string, message: string, template: number) => Promise<IFetchResponseBase>;
}

export const FirebaseContext = React.createContext<IFirebaseContext>({
	getProfile: () => Promise.resolve({ isSuccessful: false }),
	setProfile: () => Promise.resolve({ isSuccessful: false }),
	claim: () => Promise.resolve({ isSuccessful: false }),
	prepareMint: () => Promise.resolve({ isSuccessful: false }),
});



const fetchBase =
	process.env.NODE_ENV === 'development'
		? 'http://localhost:8000'
		: 'https://supsup-server.herokuapp.com';


export const FirebaseProvider: React.FC = ({ children }) => {
	/*const { jwt } = useContext(JwtContext);

	const  = useCallback(
		(path: string, request: Partial<RequestInit>) => {
			return fetch(fetchBase + path, {
				...request,
				headers:
					jwt
						? {
							...request.headers,
							Authorization: 'Bearer ' + jwt
						}
						: {
							...request.headers
						}
			});
		},
		[jwt]
	);*/




	const getProfile = useCallback(
		async (address: string): Promise<IFetchResponseBase> => {

			const fetchRes = await fetch(fetchBase + `/users/profile/${address}`, {
				method: 'GET'
			}).catch((error) => {
				console.error('Error:', error);
			});

			if (fetchRes && fetchRes.ok) {
				return await fetchRes.json();
			}
		},
		[]
	);

	const setProfile = useCallback(
		async (address: string, username: string, avatar: string): Promise<IFetchResponseBase> => {
			const fetchRes = await fetch(fetchBase + `/users/profile/${address}`, {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json'
				},
				body: JSON.stringify({ username: username, avatar: avatar })
			});

			if (fetchRes.ok) {
				return { isSuccessful: true };
			}

			return { isSuccessful: false, message: fetchRes.statusText };
		},
		[]
	);

	const claim = useCallback(
		async (address: string): Promise<IFetchResponseBase> => {
			const fetchRes = await fetch(fetchBase + `/users/claim/${address}`, {
				method: 'GET'
			});

			if (fetchRes.ok) {
				return await fetchRes.json();
			}
		},
		[]
	);

	const prepareMint = useCallback(
		async (address: string, receiver: string, message: string, template: number): Promise<IFetchResponseBase> => {
			const fetchRes = await fetch(fetchBase + `/users/prepareMint/${address}`, {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json'
				},
				body: JSON.stringify({ receiver: receiver, message: message, template: template })
			});

			if (fetchRes.ok) {
				return await fetchRes.json();
			}
		},
		[]
	);

	return (
		<FirebaseContext.Provider
			value={{
				getProfile,
				setProfile,
				claim,
				prepareMint,
			}}
		>
			{children}
		</FirebaseContext.Provider>
	);
};
